import React from 'react';
import { Layout, Menu, Icon } from 'antd';

const Sider = Layout.Sider;
const SubMenu = Menu.SubMenu;

class SiderPage extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
                <Sider>
                    <Menu mode="inline" defaultSelectedKeys={['1']}>
                        <Menu.Item key="1">
                            <Icon type="switcher" />
                            <span className="nav-text"></span>
                        </Menu.Item>
                        <SubMenu key="sub2" title={<span><Icon type="switcher" /><span className="nav-text"></span></span>}>
                            <Menu.Item key="2">
                                <Icon type="bars" />
                                <span className="nav-text"></span>
                            </Menu.Item>
                            <Menu.Item key="3">
                                <Icon type="export" />
                                <span className="nav-text"></span>
                            </Menu.Item>
                        </SubMenu>
                        <SubMenu key="sub3" title={<span><Icon type="exception" /><span className="nav-text"></span></span>}>
                            <Menu.Item key="4">
                                <Icon type="bars" />
                                <span className="nav-text"></span>
                            </Menu.Item>
                            <Menu.Item key="5">
                                <Icon type="export" />
                                <span className="nav-text"></span>
                            </Menu.Item>
                        </SubMenu>
                        <Menu.Item key="6">
                            <Icon type="bars" />
                            <span className="nav-text"></span>
                        </Menu.Item>
                        <Menu.Item key="7">
                            <Icon type="tag-o" />
                            <span className="nav-text"></span>
                        </Menu.Item>
                        <Menu.Item key="8">
                            <Icon type="scan" />
                            <span className="nav-text"></span>
                        </Menu.Item>
                        <Menu.Item key="9">
                            <Icon type="chrome" />
                            <span className="nav-text"></span>
                        </Menu.Item>
                        <SubMenu key="sub5" title={<span><Icon type="filter" /><span className="nav-text"></span></span>}>
                            <Menu.Item key="10">
                                <Icon type="filter" />
                                <span className="nav-text"></span>
                            </Menu.Item>
                            <Menu.Item key="11">
                                <Icon type="bars" />
                                <span className="nav-text"></span>
                            </Menu.Item>
                        </SubMenu>
                        <SubMenu key="sub6" title={<span><Icon type="share-alt" /><span className="nav-text"></span></span>}>
                            <Menu.Item key="10">
                                <Icon type="user" />
                                <span className="nav-text"></span>
                            </Menu.Item>
                            <Menu.Item key="11">
                                <Icon type="plus-circle" />
                                <span className="nav-text"></span>
                            </Menu.Item>
                            <Menu.Item key="12">
                                <Icon type="setting" />
                                <span className="nav-text"></span>
                            </Menu.Item>
                            <Menu.Item key="13">
                                <Icon type="team" />
                                <span className="nav-text"></span>
                            </Menu.Item>
                        </SubMenu>
                    </Menu>
                </Sider>
        );
    }
}

export default SiderPage;


